<template>
  <v-container
    fluid
  >
    <v-row>
      <v-col
        cols="12"
        md="4"
      >
        <v-card
          :loading="loading"
          class="mx-auto"
          align="center"
        >
          <v-img
            width="250"
            height="250"
            :src="src"
          >
            <template v-slot:placeholder>
              <v-row
                class="fill-height ma-0"
                align="center"
                justify="center"
              >
                <v-progress-circular
                  indeterminate
                  color="grey lighten-5"
                />
              </v-row>
            </template>
          </v-img>
          <v-card-title class="d-flex justify-space-between">
            <div class="font-weight-bold">
              {{ $t('tableName') }}：{{ servantData.tableInfo.tableName }}
            </div>
            <div>{{ servantData.tableInfo.sectionName }}</div>
          </v-card-title>
          <v-divider class="mx-2"/>
          <v-card-title class="subtitle-2">
            {{ $t('本日总结') }}
          </v-card-title>
          <v-card-text>
            <v-list
              shaped
              dense
              align="left"
            >
              <v-list-item-group
                v-model="currentIndex"
                color="primary"
              >
                <v-list-item
                  v-for="(item, i) in infoList"
                  :key="i"
                >
                  <v-list-item-content>
                    <v-list-item-title v-text="item.text"/>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col
        cols="12"
        md="8"
      >
        <v-container
          id="data-tables"
          class="fill-height align-start"
          fluid
          tag="section"
        >
          <v-container
            fluid
          >
            <base-material-card
              class="px-5 py-3 "
              color="indigo"
              icon="mdi-vuetify"
              inline
            >
              <template v-slot:after-heading>
                <div class="display-2 font-weight-light">
                  {{ $t('本日账单') }}
                </div>
              </template>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                class="ml-auto"
                hide-details
                :label="$t('Search')"
                single-line
                style="max-width: 250px;"
              />
              <v-divider class="mt-3"/>
              <v-data-table
                :fixed-header="true"
                :headers="headers"
                :items="desserts"
                :items-per-page="10"
                :loading="loading"
                :search.sync="search"
                multi-sort
              >
                <template v-slot:item.paymentStatus="{ item }">
                  <v-checkbox
                    :readonly="true"
                    :input-value="item.paymentStatus==='1'"
                    disabled
                  />
                </template>
              </v-data-table>
              <v-row>
                <v-spacer/>
                <v-btn
                  class="mb-2"
                  color="primary"
                  dark
                  @click="goback()"
                >
                  {{ $t('返回') }}
                </v-btn>
              </v-row>
            </base-material-card>
          </v-container>
        </v-container>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { getDetailTable } from '../../components/Utlis/api'
  import GlobalSettings from '@/GlobalSettings'

  export default {
    props: {
      id: {
        default: -1,
        type: Number,
      },
    },
    data: function () {
      return {
        loading: false,
        currentIndex: 0,
        tableId: this.id,
        servantData: {
          tableInfo: {
            tableName: '',
          },
        },
        infoList: [],
        search: '',
        headers: [
          {
            text: 'OrderID',
            align: 'start',
            sortable: false,
            value: 'orderId',
          },
          {
            text: this.$i18n.t('servantName'),
            value: 'servantName',
          },
          {
            text: this.$i18n.t('time'),
            value: 'eatTime',
          },
          {
            text: this.$i18n.t('price'),
            value: 'totalPrice',
          },
          {
            text: this.$i18n.t('paymentStatus'),
            value: 'paymentStatus',
          },
        ],
        desserts: [],
        dates: [],
      }
    },
    computed: {
      src: function () {
        if (this.servantData.tableInfo) {
          return GlobalSettings.SQR + this.$store.getters.rootUrl + GlobalSettings.genSQUrl + 'TableName=' +
            this.servantData.tableInfo.tableName
        } else {
          return false
        }
      },
    },
    created () {
      this.loadDetail(this.tableId)
    },
    mounted () {
      // this.loadDetail(this.servantId)
    },
    methods: {
      loadDetail (id) {
        getDetailTable(id).then(res => {
          this.servantData = res
          var usageStatus = this.servantData.tableInfo.usageStatus
          if (usageStatus === 0) {
            usageStatus = this.$i18n.t('可使用')
          } else {
            usageStatus = this.$i18n.t('已使用')
          }
          this.infoList.push({ text: this.$i18n.t('seatCount') + ':' + this.servantData.tableInfo.seatCount })
          this.infoList.push({ text: this.$i18n.t('usageStatus') + ':' + usageStatus })
          this.infoList.push({ text: this.$i18n.t('usageCount') + ':' + this.servantData.tableInfo.todayUsageCount })
          this.desserts = this.servantData.todayBill
        })
      },
      goback () {
        this.$router.history.go(-1)
      },
    },
  }
</script>

